import { Component, OnInit, Input } from '@angular/core';
import { CustomerService } from '../customer.service';
import { Customer } from '../customer';


import * as CryptoJS from 'crypto-js';

@Component({
  selector: 'app-customer-details',
  templateUrl: './customer-details.component.html',
  styleUrls: ['./customer-details.component.css']
})
export class CustomerDetailsComponent implements OnInit {

  @Input() customer: Customer;

  plainText:string;  
  encryptText: string;  
  encPassword: string;  
  decPassword:string;  

  

  constructor(private customerService: CustomerService) {



   }

  ngOnInit() {

    this.decPassword = "123";

    this.customer.age = CryptoJS.AES.decrypt(this.customer.age, this.decPassword.trim()).toString(CryptoJS.enc.Utf8); ;
  }

  updateActive(isActive: boolean) {
    this.customerService
      .updateCustomer(this.customer.key, { active: isActive })
      .catch(err => console.log(err));

      
  }

  deleteCustomer() {
    this.customerService
      .deleteCustomer(this.customer.key)
      .catch(err => console.log(err));
  }

}
