import { Component, OnInit } from '@angular/core';

import { FormGroup, FormControl, Validators } from '@angular/forms';
import { AngularFireStorage } from '@angular/fire/storage';
import { finalize } from "rxjs/operators";

import { MediaService } from 'src/app/shared/media.service';

export interface IMedia {
  title: string;
  src: string;
  type: string;
}


@Component({
  selector: 'app-media',
  templateUrl: './media.component.html',
  styleUrls: ['./media.component.css']
})
export class MediaComponent implements OnInit {

  imageList: any[];
  rowIndexArray: any[];



  constructor(private storage: AngularFireStorage, private service: MediaService) { }

  ngOnInit() {
    this.service.getMediaDetailList();

    this.service.mediaDetailList.snapshotChanges().subscribe(
      list => {
        this.imageList = list.map(item => { return item.payload.val(); });
        this.rowIndexArray =  Array.from(Array(Math.ceil((this.imageList.length+1) / 3)).keys());
      }
    );
  }



}
